button:hover {
  cursor: pointer;
}

input:focus,
textarea:focus {
  outline: none;
  box-shadow: 0px 0px 1px #f0860c;
  transform: scale(1.025);
}
button{
  color:#c4c4c4;
}
button:focus{
  color:#f0860c;
}

img:hover,button:hover{

  -ms-transform: scale(1.1); /* IE 9 */
  -webkit-transform: scale(1.1); /* Safari 3-8 */
  transform: scale(0.9);
  animation: App-logo-spin infinite 20s linear;
}