

@media only screen and (max-width: 768px) {
    .wrappertext{
        padding-top:50px;
        display:flex;
        justify-content: space-between;
        flex-direction: column;
        gap:20px;
        /* background:red; */
        height:70vh;
        
    
    }
    .wrappedtext{
 
    
        font-size:20px;
        /* color:#c4c4c4; */
        color:#4c4c4c;
        width:60%;
        margin: 0% auto;
        height:50%;
       
 
    }
    .imageWrapper{
        width:100%;
        margin:auto;
     
        /* margin-left:20%; */
        
    }
   
  }



  @media only screen and (min-width: 1024px) {
    .wrappertext{
        padding-top:100px;
        /* display:flex; */
        justify-content: space-between;
        height:40vh;
        /* background:red; */
    }
    
    .wrappedtext{
     
        
        font-size:50px;
        /* color:#c4c4c4; */
        color:#4c4c4c;
        width:45%;
        margin-top: 5%;
        margin-left: 5%;
        float: left;
        /* background:red; */
        
        
        
    }
    
    
    .imageWrapper{
        width:50%;
        float:right;
        clear: right;
        /* background:red; */
        
        
    }
    
  
   
  }




